<template>
  <main>
  <ProjectTemplate
    info="Crimson Quest is a RPG game where students create their avatar based on their academic and extracurricular interests and creates catered journey for them. 
    The application provides an easily discoverable solution for prospective students to get the relevant student-life information with rewards-based incentives, 
    as often lacked in the traditional campus tour. Adapted from a course project at"
        infoo="with"
        classinfo="Harvard's CS 79, Designing Usable Interface."
        teammate="Ishaan Prasad, Michaela Bibby, Natnael Teshome, and Vinay Kanumuri."
        challenge="We interviewed 15 different tourist groups including: couples walking through the area, visitors from China touring MIT and Harvard, a group of Thai teachers, 
        a group of Spanish speakers, parents with children, and prospective students (with their families). We synthesized our findings into the following key insights: 
        1) Visitors want flexibility and optionality when exploring campus. 2) Prospective students and their families want to learn about the day-to-day experience of attending Harvard.
        3) Prospective students and their families were keen to connect with current students to learn more about their experiences.
        4) Navigating Harvard's semi-urban campus environment yields additional obstacles for visitors."
        story="We decided to target our product towards aspiring Harvard students, especially prospective college students or accepted students who are exploring 
        their college choices and academic interests, while anticipating it to be usable by larger groups. The product aims to solve the problem that prospective 
        college students often find it difficult to imagine their college life and understand the day-to-day experience of current students. During the traditional 
        historical tour, it is hard to 1) learn more about pragmatic details of campus life and 2) explore their personal interests (due to the size of the group). 
        Therefore, by knowing their interests, the app can provide information that is more relevant to them and make their campus tour more efficient. Likewise,
         through discounts with partnered businesses and organizations, users would be more incentivized to use the product."
        testing="We laid out two main hypotheses for the product. 1) Prospective students want to experience Harvard students' life when visiting the campus. 
        2) Prospective students and their families will find the promotional discounts and prizes to be a useful incentive for using the app and the Harvard Shop 
        (and/or other local businesses) would be willing to provide these discounts. We proved these hypotheses by showing the storyboards to prospective students on campus. 
        Nearly all the prospective students expressed interests in knowing more about students' campus life and purchasing university merchandise at a discount. 
        Furthermore, we are able to gauge viability with Harvard shop staffs. "
        text="Crimson Quest is a mobile role playing game for prospective Harvard students visiting campus to learn more about the pragmatic, 
        day-to-day experience of current undergraduates. To begin, students are asked to pick a persona based on their academic and/or extracurricular interests 
        (e.g., a student interested in computer science may choose the STEM persona). Based on the persona chosen, students receive a series of tasks to guide 
        them through relevant areas on campus. An aspiring STEM student, for instance, may be tasked with taking pictures of the Harvard Mark 1 Automatic Sequence 
        Controlled Calculator (ASCC), a computer that STEM undergraduates typically learn about in CS classes. Along the way, Harvard Quest will highlight lecture 
        halls and other relevant points of interests through the use of push notifications, providing the user with the freedom to explore campus, unencumbered by 
        screens and related digital distractions. For areas that are off-limits to visitors (e.g., Sanders Theater), Harvard Quest will provide AR/VR experiences 
        for users to experience and immerse themselves within these locations. Following the successful completion of a predetermined number of tasks, users receive
         discount codes and other rewards for redemption at the Harvard Shop and other participating local businesses and organizations."
        appendix="workflow: user interview, mind mapping, storybording, prototying, and usability testing."
         />

