<template>
    <div id="vue-canvas3"></div>
  </template>
  
  <script>
  export default {
    name: "Clock1",
    mounted() {
      const script = function(p5) {
        const hourRadius = 100;
        const hourSize = 25;
        const minuteRadius = 75;
        const minuteSize = 15;
        const secondRadius = 50;
        const secondSize = 10;
  
        // NOTE: Set up is here
        p5.setup = _ => {
          const canvas = p5.createCanvas(500, 500);
          canvas.parent("vue-canvas3");
        };
        // NOTE: Draw is here
        p5.draw = _ => {
         const screenCenter = p5.createVector(p5.width/2, p5.height/2);
          p5.background(255);
          p5.noFill();
          p5.stroke(0);
          p5.ellipse(p5.width/2, p5.height/2,hourRadius*2,hourRadius*2);
          let hourCenter = analogClock(p5.hour() % 12, 12, screenCenter, hourRadius);
          p5.fill(0);
          p5.ellipse(hourCenter.x, hourCenter.y, hourSize, hourSize);
          p5.noFill();
          p5.ellipse(hourCenter.x, hourCenter.y, minuteRadius*2,minuteRadius*2);
            
          let minuteCenter = analogClock(p5.minute(), 60, hourCenter, minuteRadius);
          p5.fill(0);
          p5.ellipse(minuteCenter.x, minuteCenter.y, minuteSize, minuteSize);
          p5.noFill();
          p5.ellipse(minuteCenter.x, minuteCenter.y, secondRadius*2,secondRadius*2);
          
          let secondCenter = analogClock(p5.second(), 60, minuteCenter, secondRadius);
          p5.fill(0);
          p5.ellipse(secondCenter.x, secondCenter.y, secondSize, secondSize);
          p5.noFill();
          p5.ellipse(secondCenter.x, secondCenter.y,secondRadius,secondRadius);
            
          p5.noStroke();
          p5.fill(0);
          p5.textAlign(p5.CENTER, p5.CENTER);
          p5.text(hoursMinutesSeconds(), p5.width/2, 50);
        };
        function analogClock(value, high, mid, radius) {
            var angle = p5.map(value, 0, high, p5.radians(-90), p5.radians(270));
            return p5.createVector(mid.x + p5.cos(angle)*radius, mid.y + p5.sin(angle)*radius);
        };

        // return hours that read 1 through 12 rather than 0 through 23
        function twelveHour() {
        let h = p5.hour() % 12;
        if (h === 0) {
            h = 12;
        }
        return h;
        };

        // format hours, minutes, and seconds
        function hoursMinutesSeconds() {
        return twelveHour() + ':' + p5.nf(p5.minute(), 2) + ':' + p5.nf(p5.second(), 2);
        };
      };
      // NOTE: Use p5 as an instance mode
      const P5 = require("p5");
      new P5(script);
    }
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  #vue-canvas3 {
    display: block;
    margin: 0 auto;
    padding: 0;
    width: 500px;
    height: 500px;
    overflow: hidden;
  }
  </style>
  