<template>
    <div id="vue-canvas4"></div>
  </template>
  
  <script>
  export default {
    name: "Clock4",
    mounted() {
      const script = function(p5) {
        const outerSize = 37; // radius of the outer circle
        const hourDistance = 16;
        const hourSize = 20;
        const minuteDistance = 12;
        const minuteSize = 8;
        
        let screenCenter;
        // NOTE: Set up is here
        p5.setup = _ => {
          const canvas = p5.createCanvas(300, 300);
          canvas.parent("vue-canvas4");
          screenCenter = p5.createVector(0, 0);
            p5.ellipseMode(p5.RADIUS);
            p5.noStroke();
        };
        // NOTE: Draw is here
        p5.draw = _ => {
         const screenCenter = p5.createVector(p5.width/2, p5.height/2);
          p5.background(255);
          p5.translate (p5.width/2,p5.height/2);
          p5.stroke(0);
          p5.fill(255);

          p5.beginShape();
          p5.vertex(-80,0);
          p5.bezierVertex(-30,-50,30,-50,80,0);
          p5.bezierVertex(30,50,-30,50,-80,0)
          p5.endShape();

          p5.translate (-p5.width/2,-p5.height/2);  
          p5.fill(255);
          p5.ellipse(screenCenter.x, screenCenter.y, outerSize, outerSize);
        
          p5.fill(0);
          let hourCenter = analogClock(p5.hour() % 12, 12, screenCenter, hourDistance);
            p5.ellipse(hourCenter.x, hourCenter.y, hourSize, hourSize);
        
            p5.fill(255);
          let minuteCenter = analogClock(p5.minute(), 60, hourCenter, minuteDistance);
            p5.ellipse(minuteCenter.x, minuteCenter.y, minuteSize, minuteSize);
        //}

        p5.translate (p5.width/2,p5.height/2);
        p5.fill(0);
        p5.stroke(0);
            let secondAngle = p5.map(p5.second(), 0, 60, p5.radians(-90), p5.radians(270));
            p5.rotate(secondAngle);
            p5.ellipse(0,100,minuteSize,minuteSize);
            }
            
            function analogClock(value, high, mid, radius) {
            var angle = p5.map(value, 0, high, p5.radians(-90), p5.radians(270));
            return p5.createVector(mid.x + p5.cos(angle)*radius, mid.y + p5.sin(angle)*radius);
            }

            // return hours that read 1 through 12 rather than 0 through 23
            function twelveHour() {
            let h = p5.hour() % 12;
            if (h === 0) {
                h = 12;
            }
            return h;
            };

            // format hours, minutes, and seconds
            function hoursMinutesSeconds() {
            return twelveHour() + ':' + p5.nf(p5.minute(), 2) + ':' + p5.nf(p5.second(), 2);
            };
        };
        // NOTE: Use p5 as an instance mode
        const P5 = require("p5");
        new P5(script);
        }
    };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  #vue-canvas4 {
    display: block;
    margin: 0 auto;
    padding: 0;
    width: 300px;
    height: 300px;
    overflow: hidden;
  }
  </style>
  