<!-- Default page that also displays projects -->

<template>
    <main>
      <div class="left-half">
        <div role="tablist">
          <b-card no-body class="mb-1">
            <b-card-header header-tag="header" class="p-1" role="tab">
              <b-button block v-b-toggle.accordion-1 variant="info">info</b-button>
            </b-card-header>
            <b-collapse id="accordion-1" visible accordion="my-accordion" role="tabpanel">
              <b-card-body>
                {{ info }}
                  <span class="highlight-green">{{ classinfo }}</span>
                  {{ infoo }}
                  <span class="highlight-purple">{{ teammate }}</span>
              </b-card-body>
            </b-collapse>
          </b-card>
  
          <b-card no-body class="mb-1">
            <b-card-header header-tag="header" class="p-1" role="tab">
              <b-button block v-b-toggle.accordion-2 variant="info">process</b-button>
            </b-card-header>
            <b-collapse id="accordion-2" accordion="my-accordion" role="tabpanel">
              <b-card-body>
                <!-- second collapse -->
                <div role="tablist">
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2-1 variant="warning">I. the challenge</b-button>
                    </b-card-header>
                    <b-collapse id="accordion-2-1" accordion="my-accordion-2-1" role="tabpanel">
                      <b-card-body>
                        <b-card-text>{{ challenge }}</b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
  
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2-2 variant="warning">II. the story mapping</b-button>
                    </b-card-header>
                    <b-collapse id="accordion-2-2" accordion="my-accordion-2-2" role="tabpanel">
                      <b-card-body>
                        <b-card-text>
                          {{ story }}
                        </b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
  
                  <b-card no-body class="mb-1">
                    <b-card-header header-tag="header" class="p-1" role="tab">
                      <b-button block v-b-toggle.accordion-2-3 variant="warning">III. the user testing</b-button>
                    </b-card-header>
                    <b-collapse id="accordion-2-3" accordion="my-accordion-2-3" role="tabpanel">
                      <b-card-body>
                        <b-card-text>{{ testing }}</b-card-text>
                      </b-card-body>
                    </b-collapse>
                  </b-card>
                </div>
              <!-- second collapse end -->
              </b-card-body>
            </b-collapse>
          </b-card>
  
          <b-card no-body class="mb-1">
            <b-card-header header-tag="header" class="p-1" role="tab">
              <b-button block v-b-toggle.accordion-3 variant="info">product</b-button>
            </b-card-header>
            <b-collapse id="accordion-3" accordion="my-accordion" role="tabpanel">
              <b-card-body>
                {{ text }}
              </b-card-body>
            </b-collapse>
          </b-card>

          <b-card no-body class="mb-1">
            <b-card-header header-tag="header" class="p-1" role="tab">
              <b-button block v-b-toggle.accordion-4 variant="appdix">*</b-button>
            </b-card-header>
            <b-collapse id="accordion-4" accordion="my-accordion" role="tabpanel">
              <b-card-body>
                {{ appendix }}
              </b-card-body>
            </b-collapse>
          </b-card>

        </div>  
      </div>
    

  
    </main>
  </template>
  
  <script>
  import { CollapsePlugin } from 'bootstrap-vue';
  
  export default {
    name: 'ProjectTemplate',
    components: {CollapsePlugin},
    props: {
          info: String,
          infoo: String,
          classinfo: String,
          teammate: String,
          challenge: String,
          story: String,
          testing: String,
          text: String,
          appendix: String
          
      }
  }
  </script>
  
  <style scoped>
  .left-half{
    position: absolute;
    left: 0px;
    width: 50%;
    height: 100%;
    padding-left: 10%;
    padding-top: 10%;
    padding-bottom: 10%;
    overflow-y: auto;
  }

  .btn {
    --bs-btn-color: none;
    --bs-btn-bg: none;
    --bs-btn-border-color: none;
    --bs-btn-hover-bg: none;
    --bs-btn-hover-border-color: none;
    --bs-btn-active-bg: none;
    --bs-btn-active-border-color: none;
    font-size: calc(4rem);
  }
  .btn[aria-expanded="true"]{
    text-decoration: underline;
  }
  .card {
    --bs-card-color: none;
    --bs-card-border-width: 0;
    --bs-card-cap-bg: none;
  }
  .btn-warning {
    font-size: calc(1.5rem);  
    font-weight: bold;
  }
  .btn-warning[aria-expanded="true"]{
    text-decoration: none;
  }

  @media only screen and (max-width: 480px) {
    .btn {
    font-size: calc(2rem);
    }
    .btn-warning {
    font-size: calc(1rem);  
    }
    .left-half{
    position: absolute;
    top: 50%;
    left: 0px;
    width: 100%;
    height: 100%;
    padding-left: 10%;
    padding-top: 10%;
    padding-bottom: 10%;
    overflow-y: auto;
  }

}
  
  </style>
  