<template>
    <div id="vue-canvas5"></div>
  </template>
  
  <script>
  export default {
    name: "Clock5",
    mounted() {
      const script = function(p5) {
        // NOTE: Set up is here
        p5.setup = _ => {
          const canvas = p5.createCanvas(300, 300);
          canvas.parent("vue-canvas5");
        };
        // NOTE: Draw is here
        p5.draw = _ => {
            p5.background(255);
  
            p5.stroke(0);
            
            let n = 12*60;
            for (let t = 0; t < n; t++) {
                let hr = p5.map(t%720, 0, 11, 0, 2*Math.PI);
                let hrr = p5.map(p5.hour()%12, 0, 11, 0, 2*Math.PI);
                let min = p5.map(t%60, 0, 59, 0, 2*Math.PI);
                let minn = p5.map(p5.minute(), 0, 59, 0, 2*Math.PI);
                let sec = p5.map(p5.second(), 0, 59, 0, 2*Math.PI);
                let x = p5.sin(min+sec) * 5* Math.abs(30-p5.second()) + p5.width/2;
                let y = -p5.cos(min-sec) * 60 + p5.map(t, 0, n-1, 1, 0.1) + p5.height / 2;
                let rad = getSize(t);
                let hh = p5.hour() % 12;
                let now = hh*60 + p5.minute();
                p5.fill(0);
                p5.stroke(255);
                p5.ellipse(x, y, 3, 3);
                
                let mx = p5.sin(min) * 3 * (Math.abs(30-p5.minute())+10) + p5.width/2;
                let my = -p5.cos(-min) * 30 + p5.height / 2;
                p5.ellipse(mx, my, 3, 3);
                
                let hx = p5.sin(hr) * (Math.abs(6-p5.hour())+3) + p5.width/2;
                let hy = -p5.cos(-hr) * 6 + p5.height / 2;
                p5.ellipse(hx, hy, 3, 3);
                
                p5.stroke(255);
                p5.fill(0);
                let sx = p5.sin(sec) * 5 * Math.abs(30-p5.second()) + p5.width/2;
                let sy = -p5.cos(-sec) * 60 + p5.height / 2;
                p5.ellipse(sx, sy, 6, 6);
                
                p5.stroke(0);
                p5.fill(255);
                let mmx = p5.sin(minn) * 3 * (Math.abs(30-p5.minute())+10) + p5.width/2;
                let mmy = -p5.cos(-minn) * 30  + p5.height / 2;
                p5.ellipse(mmx, mmy, 9, 9);          

                let hhx = p5.sin(hrr) * (Math.abs(6-p5.hour())+3) + p5.width/2;
                let hhy = -p5.cos(-hrr) * 6 + p5.height / 2;
                p5.ellipse(hhx, hhy, 12, 12);
                }
            }

            function getSize(n){
            let hh = p5.hour() % 12;
            let now = hh*60 + p5.minute();
            if (now>=n) {
                return 5;}
            else {
                return 0;}
            }
        };
        // NOTE: Use p5 as an instance mode
        const P5 = require("p5");
        new P5(script);
        }
    };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  #vue-canvas5 {
    display: block;
    margin: 0 auto;
    padding: 0;
    width: 300px;
    height: 300px;
    overflow: hidden;
  }
  </style>
  