        <div class="playbox" id="box-hl">
          <h3 class="p-title">HarvardLink </h3>
          <img class="lozad projectimg" src="..\public\hl1.jpg" alt="">
          <p> The current Harvard Shuttle system has circuitous route, confusing time schedule and long intervals between shuttles. The difficulties of traveling between campuses set obstacles for students to attend courses and social events. 
            HarvardLink re-imagines the Harvard Shuttle experience to provide more accessibility and social opportunities for students and faculties.
            We interviewed 12 students and identified two major challenges: 1) The current shuttle schedule doesn't correspond to students' schedule;
            2) The accessibility needs are not well addressed.
                Adapted from a course project at
                  <span class="highlight-green">Harvard CS 79</span>
                  with
                  <span class="highlight-purple">Natnael Teshome and Natalia Calvo.</span>
                  Process includes service design, persona development, information architecture, journey map, UX prototype, and user testing. </p>
                  <iframe src="https://www.figma.com/embed?embed_host=share&url=https%3A%2F%2Fwww.figma.com%2Ffile%2FvLzDp6KwfGcszxUSZsPlBD%2FUntitled%3Fnode-id%3D0%253A1%26t%3DR3ATAIhub4KDNTWj-1" width="400" height="250" allowfullscreen></iframe>
            <p> 
            The new shuttle experience to provide more fluid experience for students, more accessibility accommodations and more efficient uses of shuttles. The main solutions provided to solve the existing problems are:
            1. Recursive scheduling feature. Students can import their class schedules to order the recursive shuttles. They can also add future one-time trip to the schedule. The scheduling features will help the system better allocate the shuttle services to when/where the shuttle is more needed to avoid overcrowding issues.
            2. Add travel buddy features. Students can find travel companies that will take the same route with them. When two people become travel buddies, they will be assigned to the same shuttle. The feature solves the need that many students want to schedule trips with their friends, but also provides accessibility assistance to students with visual/ audio/ physical impairments without the stigma of claiming the needs of extra help.
            </p>
