    info="Standup is a unique team project management app for students to sync their work.
    Standup creates a supportive, responsive and motivational teamwork environment for students by 
    encouraging more communication and reaction among teammates.  
    
    The project is a fully interactive web app with both backend and frontend implemented for the course"
        classinfo="MIT 6.1040 Software Studio"
        infoo="with"
        teammate="Amy Lei, Ivy Mao, and Ophelia Zhu."
        challenge="Asynchronously organizing and tracking group project progress can be really difficult. There are countless 
        applications existing for managing team projects. However, none of them are really useful for students working on group 
        projects at school. Firstly, apps like notion and trello are heavy-weighted, requiring a lot of effort to set up. 
        Enterprises usually have a fixed top-down fixed workflow, while student projects have much more diverse structures 
        and students want more freedom in organizing their time. Secondly, traditional planning apps are not as effective for 
        students, as we often find it difficult to hold teammates accountable for checkpoints. "
        story="To facilitate the software development process of backend and frontend, we laied out five main concepts in the product. 
        Firstly, the 'Project' is the overarching folder for organizing updates and teammates. Secondly, the 'User' authenticates users 
        and stores personal information. Thirdly, the 'Update' serves as a standardized unit of progress information. Fourthly, 
        the 'Eyes Wanted' allow users to flag or bring immediate attention to certain items. Lastly, the 'Thanks' shows appreciation 
        for teammates who are on top of their work. By laying out the operational principle, states and actions of each concept, 
        we develop the framework to implement the backend of the app."
        testing="We created five user tasks for the user to perform: join an existing project; create an update; call teammate's 
        attention to the update; show appreciation to teammate's update, and create a new project. By observing the user performing
        these tasks, we identified several flaws on conceptual, physical and linguistic level, and adjusted the interface 
        accordingly. "
        text="
        Introducing CYNK, a unique team project management for students. Unlike traditional enterprise project management apps 
        that require a team manager to set up checkpoints in a top-down manner, CYNK encourages students to post their progress as desired 
        either to keep record for themselves, or to call teammates' attention. Students can post “updates” in a blog format with tags, 
        which can be reviewed and reacted to by teammates. Users can remind their teammates when they haven't updated their progress for 
        a long time, or require their teammates to react to their updates, in order to keep everyone on track. On the other hand, students 
        are also encouraged to express appreciation towards each others' work and to learn from each other.
Through encouraging students to review each others' progress and provide feedback, we believe CYNK can facilitate healthier teamwork among 
students and create a more participatory learning environment."
        appendix="workflow: needfinding, user interview, conceptual design, VSD analysis, UX prototype, 
        user testing, background development using MongoDB and TypeScript, frontend development using Vue.js."
