<template>
    <div id="vue-canvas"></div>
  </template>
  
  <script>
  export default {
    name: "Clock3",
    mounted() {
      const script = function(p5) {
        // NOTE: Set up is here
        p5.setup = _ => {
          const canvas = p5.createCanvas(800, 300);
          canvas.parent("vue-canvas");
        };
        // NOTE: Draw is here
        p5.draw = _ => {
          let n = 12*60;
          for (let t = 0; t < n; t++) {
            let min = p5.map(t%60, 0, 59, 0, 2*Math.PI);
            let x = p5.sin(min) * 30 + p5.map(t, 0, n-1, 30, p5.width-30);
            let y = -p5.cos(min) * 80 + p5.map(t, 0, n-1, 1, 0.1) + p5.height / 2;
            let rad = getSize(t);
            let hh = p5.hour() % 12;
            let now = hh*60 + p5.minute();
            if (t == now){
              p5.fill(0);
              let secondAngle = p5.map(p5.second(), 0, 60, p5.radians(-90), p5.radians(270));
              p5.arc(x, y, 20, 20, p5.radians(-90), secondAngle);
            }
            else {
              p5.fill(0);
              p5.stroke(255);
              p5.ellipse(x, y, rad, rad);
            }
          }
          // p5.text(hoursMinutesSeconds(), p5.width/2, 50);
          // p5.noStroke();
          // p5.fill(0);
          // p5.textFont('Minion Pro', 30);
          // p5.textAlign(p5.CENTER, p5.CENTER);
          // p5.text(hoursMinutesSeconds(), p5.width/2, p5.height/2-100);
        }

        function getSize(n){
          let hh = p5.hour() % 12;
          let now = hh*60 + p5.minute();
          if (now>=n) {
            return 20;}
          else {
            return 3;}
        }
        // return hours that read 1 through 12 rather than 0 through 23
        function twelveHour() {
        let h = p5.hour() % 12;
        if (h === 0) {
            h = 12;
        }
        return h;
        };

        // format hours, minutes, and seconds
        function hoursMinutesSeconds() {
        return p5.hour() + ':' + p5.nf(p5.minute(), 2) + ':' + p5.nf(p5.second(), 2);
        };
      };
      // NOTE: Use p5 as an instance mode
      const P5 = require("p5");
      new P5(script);
    }
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  #vue-canvas {
    display: block;
    margin: 0 auto;
    padding: 0;
    width: 800px;
    height: 300px;
    overflow: hidden;
  }
  </style>
  