var render = function render(){var _vm=this,_c=_vm._self._c;return _c('main',[_c('div',{staticClass:"phone-proj"},[_c('ProjectTemplate',{attrs:{"info":"CaterHub is an all-in-one catering app created to streamline the current cumbersome group ordering process. \n    By providing an efficient and transparent solution to gather multiple food preferences and make orders, \n    CaterHub will build a diverse workplace by celebrating different tastes.","infoo":"The original project was a team project with","classinfo":"Harvard's CS 79, Designing Usable Interface.","teammate":"Natnael Neshome and Ivan Zhang.","challenge":"Catered meals provide a valuable opportunity for companies that want to boost employee morale and subsequently \n      improve retention - which is increasingly difficult today. In fact, in 2022 the catering industry had a market of $10.6 billion. \n      However, the catering process is full of holes. Manual attempts consist of gathering food preferences, finding restaurants, then finding their \n      contact information, then making sure that the restaurant caters, then ensuring the order is correct, and so forth. We want to design an effective and efficient product to facilitate \n      the catering process by creating better coordination and workflows between organizers of events, restaurants, and the people for \n      whom the food is catered.\n      To understand the challenges in this process, we conducted 15 interviews in 6 stakeholder groups.","story":"To target the three major problems we identified through the stakeholder interviews, we propose three high-level solutions. \n      1) For organizers who find it hard to gather information about catering restaurants, we provide the platform where local food suppliers \n      can post their information to be easily accessible to companies; \n      2) For organizers who find it hard to get individual preferences and dietary restrictions, we design the process for individuals to accept organizer's \n      invitation and add their individual orders to facilitate the decision making process; 3) For the problem that catering often leads to food waste, \n      we provide the feature that organizations and individuals can alert their food leftover, so that their colleagues in need can help consume the \n      leftover food. The feature can also be used for employees to initiate food sharing sessions for bonding.","testing":"We revisited the office assistant that we initially interviewed for the needfinding assignment. During this interview \n      we were primarily concerned with the ease of use of the product, inclusiveness, and whether the product was missing any features. \n      With respect to the ease of use, the assistant mentioned that the “login flow was very quick” and brought him to an equally intuitive \n      company main page where they got a global view of all the restaurants and their specifications (i.e. minimum order size). \n      With regards to the inclusiveness, the office assistant was “very impressed and excited” about the customer flow where employees could \n      upvote favorite restaurants and dishes. He mentioned that they “always argue over where to order from” and that the upvoting is “perfect” \n      for that use case. He continued by noting that he was “extremely happy” about the inclusion of a dietary restrictions and notes section \n      for employees when they upvote an item. ","text":"Our final product concept is an all-in-one catering marketplace and planning app with a key focus of making it easier for companies to \n      order large group meals. In the app, local caterers and restaurants can provide their menu as well as catering specifications for companies. \n      These catering specifications would allow restaurants to note what dietary restrictions and food preferences they can cater for. \n      Office managers can plan their future events and send out invitation to employees. Then employees can bookmark restaurants and \n      items that they are interested in and would love to order a meal from. In addition, employees can downvote restaurants they don't \n      like and even reject restaurants that they cannot eat at. This information is surfaced to office administrators who then organize a meal around \n      these preferences. Finally, after the meal, the office administrator can see the history of all the group orders. Administrators and employees can \n      share information about leftover food to avoid food waste.","appendix":"workflow: contextual interviews, affinity diagraming, interactive demo, hypothesis testing, user testing, pitch, \n      and writing a 40-page, 13,634 words report."}}),_c('div',{staticClass:"right-half"},[_c('b-collapse',{attrs:{"id":"accordion-1","visible":"","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('img',{staticClass:"lozad projectimg phone-img",attrs:{"src":require("..\\public\\cater1.png"),"alt":"caterhub landing page"}})])],1),_c('b-collapse',{attrs:{"id":"accordion-2","accordion":"my-accordion","role":"tabpanel"}},[_c('b-card-body',[_c('b-collapse',{attrs:{"id":"accordion-2-1","accordion":"my-accordion-2-1","role":"tabpanel"}},[_c('b-card-body',[_c('iframe',{staticStyle:{"border":"1px solid rgba(0, 0, 0, 0.1)"},attrs:{"width":"800","height":"800","src":"https://www.figma.com/embed?embed_host=share&url=https%3A%2F%2Fwww.figma.com%2Ffile%2FiONhAFBSc0ydZh2gCYginN%2FCaterhub--challenge%3Fnode-id%3D0%253A1%26t%3DU1CNQ8RTfgRckeyC-1","allowfullscreen":""}})])],1),_c('b-collapse',{attrs:{"id":"accordion-2-2","accordion":"my-accordion-2-2","role":"tabpanel"}},[_c('b-card-body',[_c('iframe',{staticStyle:{"border":"1px solid rgba(0, 0, 0, 0.1)"},attrs:{"width":"800","height":"800","src":"https://www.figma.com/embed?embed_host=share&url=https%3A%2F%2Fwww.figma.com%2Ffile%2FYDP5JWG1nTxTHYderUZP0h%2Fcater-information-architecture%3Fnode-id%3D0%253A1%26t%3DLXLklyvP7fCPE4bc-1","allowfullscreen":""}})])],1),_c('b-collapse',{attrs: