import Vue from 'vue';
import VueRouter from 'vue-router';
import HomePage from './components/Home/HomePage.vue';
import AboutPage from './components/About/AboutPage.vue';
import ClassPage from './components/Class/ClassPage.vue';
import WorkPage from './components/Work/WorkPage.vue';
import CaterhubPage from './components/Work/CaterhubPage.vue';
import flower from './components/Work/flower.vue';
import dataflow from './components/Work/dataflow.vue';
import CYNK from './components/Work/CYNK.vue';
import tour from './components/Work/tour.vue';
import PlayPage from './components/Work/PlayPage.vue';
import NotFound from './NotFound.vue';
import Clock from './components/Class/Clock.vue';
import Weather from './components/Class/Weather.vue';
import Fabrication from './components/Class/Fabrication.vue';

Vue.use(VueRouter);

const routes = [
  {path: '/', name: 'Home', component: HomePage},
  {path: '/about', name: 'About', component: AboutPage},
  {path: '/class', name: '6438', component: ClassPage},
  {path: '/class/clock', name: 'clock', component: Clock},
  {path: '/class/weather', name: 'weather', component: Weather},
  {path: '/class/fabrication', name: 'fabrication', component: Fabrication},
  {path: '/work', name: 'Work', component: WorkPage},
  {path: '/work/caterhub', name: 'Caterhub', component: CaterhubPage},
  {path: '/work/sync', name: 'CYNK', component: CYNK},
  {path: '/work/flower', name: 'Flower', component: flower},
  {path: '/work/data', name: 'Dataflow', component: dataflow},
  {path: '/work/tour', name: 'Tour', component: tour},
  {path: '/play', name: 'Play', component: PlayPage},
  {path: '*', name: 'Not Found', component: NotFound}
];

const router = new VueRouter({routes});

/**
 * Navigation guards to prevent user from accessing wrong pages.
 */
router.beforeEach((to, from, next) => {
  if (router.app.$store) {
    if (to.name === 'Login' && router.app.$store.state.username) {
      next({name: 'Account'}); // Go to Account page if user navigates to Login and are signed in
      return;
    }

    if (to.name === 'Account' && !router.app.$store.state.username) {
      next({name: 'Login'}); // Go to Login page if user navigates to Account and are not signed in
      return;
    }
  }

  next();
});

export default router;
