<template>
    <div id="vue-canvas2"></div>
  </template>
  
  <script>
  export default {
    name: "Clock2",
    mounted() {
      const script = function(p5) {

        // NOTE: Set up is here
        p5.setup = _ => {
          const canvas = p5.createCanvas(400, 200);
          canvas.parent("vue-canvas2");
        };
        // NOTE: Draw is here
        p5.draw = _ => {
          p5.stroke(0);
          let ystart = 100;
          let xstart = 10;
          // containing box
          p5.fill(255);
          p5.stroke(0);
          let hourSize = (p5.width - 20) / 24;
          p5.rect(xstart-1,ystart-1,p5.width-10,hourSize+2);
          
        let h = p5.hour() % 24 ;
        let x = xstart;
        let y = ystart;
        for (let i = 0; i < h; i++) {
          p5.fill(0);
          p5.stroke(255);
          p5.rect(x, y, hourSize, hourSize);
          x += hourSize;
        }
        // minutes after hours
        let xm = minutesStart(hourSize, h);
        y = y + hourSize-1;
        let minuteSize = hourSize / Math.sqrt(60);
        for (let i = 0; i <= p5.minute(); i++) {
          p5.fill(0);  
          p5.stroke(255);
          p5.rect(xm, y, minuteSize, minuteSize);
          y -= minuteSize;
          if (y <= ystart) {
            xm += minuteSize;
            y = ystart + hourSize -1;
          }
          // draw seconds
          // if (i==p5.minute()){
          //   let ysecond = y - minuteSize * (60-p5.second());
          //   p5.rect(xm, ysecond, minuteSize, minuteSize);
          //   }
          }
          p5.noStroke();
          p5.fill(0);
          // p5.textAlign(p5.CENTER, p5.CENTER);
          // p5.text(hoursMinutesSeconds(), p5.width/2, p5.height/2);
          // p5.text(percentage(), p5.width/2, p5.height- 60);
          }
        
      function minutesStart(hourSize, h) {
        return 10 + hourSize * h+1;
      }
      
      function hoursMinutesSeconds() {
        return p5.hour() + ':' + p5.nf(p5.minute(), 2) + ':' + p5.nf(p5.second(), 2);
      }

      function percentage() {
        return (p5.hour()*60 + p5.minute())/(24*60)*100 + '%';
      }
    };
      // NOTE: Use p5 as an instance mode
      const P5 = require("p5");
      new P5(script);
  }
  };
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  #vue-canvas2 {
    display: block;
    margin: 0 auto;
    padding: 0;
    width: 400px;
    height: 200px;
    overflow: hidden;
  }
  </style>
  